/* @import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap"); */

.App {
  background-color: var(--color-background);
  color: var(--color-foreground);
}

table,
tr,
td,
th {
  border: 1px solid;
  border-collapse: collapse;
  padding: 0.5rem;
  text-align: center;
}

table {
  margin-left: auto;
  margin-right: auto;
}

#aboutSection {
  text-align: center;
}

#educationSection {
  text-align: center;
}

#experienceSection {
  text-align: center;
}

#projectSection {
  text-align: center;
}

#projectSection ul {
  display: inline-block;
  text-align: left;
}

nav {
  text-align: center;
}

.navigationIcon {
  color: black;
  padding: 5px;
  display: inline-block;
}

.nightMode {
  display: flex;
  justify-content: center;
  align-items: center;
}